@media (width >= 300px) and (width <= 450px) {
  body {
    background-color: 6586CD;
  }

  .container {
    background-color: 0F192E;
    width: 80%;
    height: 90vh;
    margin: 20px auto;
    padding-top: 5rem;
  }

  h3 {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    text-decoration: underline;

    & hr {
      display: none;
    }
  }

  .label {
    font-size: 1.2rem;
    font-weight: 400;
  }

  .user-weight, .user-height {
    background-color: 567AC8;
    color: green;
    border: none;
    padding: 1rem;
    font-size: 1rem;
  }

  .bmiBtn {
    background-color: 567AC8;
    color: #000;
    border-radius: 24px;
    width: 70%;
    padding: 1rem;
    font-size: .8rem;
    font-weight: 900;
  }

  .output {
    background-color: #fff;
    background-color: 567AC8;
    width: 90%;
    height: 8rem;
    padding: 2rem;
    font-family: roboto;
  }
}

